import React, { useState, useEffect } from 'react';

import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Divider from '@material-ui/core/Divider';
import CheckIcon from '@material-ui/icons/Check';
import OpenInNewIcon from '@material-ui/icons/OpenInNew';

import LazyLoad from 'react-lazyload';
import Loader from '../../components/Loader/Loader';

import Avatar from '@material-ui/core/Avatar';
import { Education, School } from '../../appTypes';
import { Chip } from '@material-ui/core';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        courseList: {
            width: '100%',
            maxWidth: 360,
            backgroundColor: theme.palette.background.paper,
        },
        medium: {
            width: theme.spacing(10),
            height: theme.spacing(10),
        },
        buttons: {
            fontSize: '0.6rem',
            textTransform: "none"
        },
    }),
);

const EducationPage: React.FC<Education> = (props): JSX.Element => {

    const classes = useStyles();

    const [schools, setSchools] = useState<Array<School>>([]);
    const [courses, setCourses] = useState<Array<string>>([]);

    useEffect(() => {
        setSchools(props.schools);
        setCourses(props.courses);
    }, [props.schools, props.courses]);

    return (
        <React.Fragment>
            <Box display="flex" flexWrap="wrap" justifyContent="flex-start" p={1} m={1}>
                {schools.length > 0 ?
                    schools.map((school, index) => (
                        <Box p={1} key={index} flexGrow={1}>
                            <Card>
                                <CardHeader avatar={<LazyLoad placeholder={<Loader />}><Avatar
                                    variant="rounded"
                                    className={classes.medium}
                                    alt={school.institution}
                                    src={school.logo} /></LazyLoad>}
                                    title={school.institution}
                                    subheader={school.area} />
                                <CardContent>
                                    <Typography variant="body2" color="textSecondary" component="p">
                                        {school.description}
                                    </Typography>
                                    <Typography variant="body2" color="textSecondary" component="p">
                                        {school.studyType}
                                    </Typography>
                                    <Typography variant="body2" color="textSecondary" component="p">
                                        {school.start.year} - {school.end.year}
                                    </Typography>
                                </CardContent>
                                <CardActions>
                                    <Box width="100%" textAlign="right">
                                        <Button
                                            href={school.website}
                                            className={classes.buttons}
                                            startIcon={<OpenInNewIcon />}
                                            target="_blank"
                                            size="small"
                                            variant="outlined">Institution Website</Button>
                                    </Box>
                                </CardActions>
                            </Card>
                        </Box>
                    )) : <Loader />
                }
            </Box>
            <Divider />
            <Box mt={2}>
                <Typography variant="body2" color="textPrimary" component="h1">Featured courses that I took and enjoyed.</Typography>
                {courses.length > 0 ?
                    <Box display="flex" flexWrap="wrap" p={1} m={1}>
                            {courses.map((course, index) => (
                                <Box m={1} key={index}><Chip label={course} icon={<CheckIcon />} /></Box>
                            ))}
                    </Box> : <Loader />
                }
            </Box>
        </React.Fragment>
    );
}

export default EducationPage;