import React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import HomeIcon from '@material-ui/icons/Home';
import InfoIcon from '@material-ui/icons/Info';
import ClassIcon from '@material-ui/icons/Class';
import SchoolIcon from '@material-ui/icons/School';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import WorkIcon from '@material-ui/icons/Work';

import ContactMailIcon from '@material-ui/icons/ContactMail';

import { NavLink } from 'react-router-dom';

const useStyles = makeStyles({
  list: {
    width: 'auto',
  },
  fullList: {
    width: 'auto',
  },
});

export interface MenuProps {
  isOpen: boolean;
  onClickHandler: Function;
}

const Menu: React.FC<MenuProps> = ({ isOpen, onClickHandler }): JSX.Element => {

  const classes = useStyles();

  const list = (
    <div className={clsx(classes.list)}
    role="presentation"
    onClick={() => onClickHandler(false)}
    onKeyDown={() => onClickHandler(false)}
    >
      <List>
        <ListItem component={NavLink} to="/" exact button key='Home' onClick={() => onClickHandler(false)}>
          <ListItemIcon><HomeIcon /></ListItemIcon>
          <ListItemText primary='Home' />
        </ListItem>
        <ListItem component={NavLink} to="/about-me" exact button key='AboutMe' onClick={() => onClickHandler(false)}>
          <ListItemIcon><InfoIcon /></ListItemIcon>
          <ListItemText primary='About Me' />
        </ListItem>
        <Divider />

        <ListItem component={NavLink} to="/skills" exact button key='Skills' onClick={() => onClickHandler(false)}>
          <ListItemIcon><ClassIcon /></ListItemIcon>
          <ListItemText primary='Skills' />
        </ListItem>
        <ListItem component={NavLink} to="/education" exact button key='Education' onClick={() => onClickHandler(false)}>
          <ListItemIcon><SchoolIcon /></ListItemIcon>
          <ListItemText primary='Education' />
        </ListItem>
        <ListItem component={NavLink} to="/projects" exact button key='Projects' onClick={() => onClickHandler(false)}>
          <ListItemIcon><CheckCircleIcon /></ListItemIcon>
          <ListItemText primary='Projects' />
        </ListItem>
        <ListItem component={NavLink} to="/experience" exact button key='Experience' onClick={() => onClickHandler(false)}>
          <ListItemIcon><WorkIcon /></ListItemIcon>
          <ListItemText primary='Experience' />
        </ListItem>

        <Divider />

        <ListItem component={NavLink} to="/contact-me" button key='Contact' onClick={() => onClickHandler(false)}>
          <ListItemIcon><ContactMailIcon /></ListItemIcon>
          <ListItemText primary='Contact Me' />
        </ListItem>
      </List>
    </div >
  );

  return (
    <Drawer key="1" open={isOpen} onClose={() =>onClickHandler(false)}>
      {list}
    </Drawer>
  );
}

export default Menu;