import React, { useEffect, useState } from 'react';

import LazyLoad from 'react-lazyload';
import Loader from '../../components/Loader/Loader';

import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';

import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import OpenInNewIcon from '@material-ui/icons/OpenInNew';

import Avatar from '@material-ui/core/Avatar';
import { Experiences, Experience } from '../../appTypes';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        medium: {
            width: theme.spacing(10),
            height: theme.spacing(10),
        },
        buttons: {
            fontSize: '0.6rem',
            textTransform: "none"
        }        
    }),
);

const ExperiencePage: React.FC<Experiences> = (props): JSX.Element => {

    const classes = useStyles();

    const [list, setList] = useState<Array<Experience>>([]);

    useEffect(() => {
        setList(props.data);
    }, [props.data]);

    if (list.length > 0) {
        return (
            <React.Fragment>
                <Box mt={2}>
                    <Typography variant="subtitle1" color="textPrimary" component="h2">Featured Experiences</Typography>
                </Box>
                <Box display="flex" flexWrap="wrap" justifyContent="flex-start" p={1} m={1}>
                    {list.length > 0 ?
                        list.map((work, index) => (
                            <Box p={1} key={index} flexGrow={1}>
                                <Card>
                                    <CardHeader
                                        avatar={<LazyLoad placeholder={<Loader />}><Avatar
                                            variant="rounded"
                                            className={classes.medium}
                                            alt={work.company}
                                            src={work.logo} /></LazyLoad>}
                                        title={work.position}
                                        subheader={work.company + " / " + work.location}
                                        action={work.start.month + "-" + work.start.year + " / " + work.end.month + "-" + work.end.year} />
                                    <CardContent>
                                        <Typography variant="body2" color="textPrimary" component="p">
                                            {work.summary}
                                        </Typography>
                                    </CardContent>
                                    <CardActions>
                                        <Box width="100%" textAlign="right">
                                            <Button
                                                className={classes.buttons}
                                                href={work.website}
                                                startIcon={<OpenInNewIcon />}
                                                target="_blank"
                                                size="small"
                                                variant="outlined">Company Website</Button>
                                        </Box>
                                    </CardActions>
                                </Card>
                            </Box>
                        )) : <Loader />
                    }
                </Box>
                <Box mt={2} pb={2}>
                    <Typography variant="subtitle1" color="textPrimary" component="h2">Others</Typography>
                    <p>Under construction...</p>
                </Box>
            </React.Fragment>
        );
    }
    else {
        return (
            <Loader />
        );
    }

};

export default ExperiencePage;