import React, { useEffect, useState } from 'react';

import { Basics } from '../../appTypes';

import Loader from '../../components/Loader/Loader';

import { Box } from '@material-ui/core'

import Typist from 'react-typist';
import { Typography } from '@material-ui/core';

import BackgroundSlider from 'react-background-slider';
import image1 from '../../assets/images/image1.jpg';
import image2 from '../../assets/images/image2.png';

import './HomePage.css';

const HomePage: React.FC<Basics> = (props): JSX.Element => {

    const [list, setList] = useState<Array<string>>([]);

    useEffect(() => {
        if (props.data.name !== '') {
            let lines: Array<string> = [];
            lines.push(props.data.name);
            lines.push(' ');
            lines.push(props.data.label);
            lines.push(' ');
            lines.push(props.data.headline);
            setList(lines);
        }
    }, [props.data]);

    const bgImages = [image1, image2];
    return (
        <React.Fragment>
            <Box width={'80%'} m="auto" mt={2} p={2} textAlign="center" borderRadius="10px" color="grey" bgcolor="rgba(255,255,255,0.5)">
                {list.length > 0 ?
                    <Typist startDelay={2000} cursor={{ hideWhenDone: true }}>
                        {list.map((text, index) => { return (<Typography variant="h5" component="span" key={index}>{text}<Typist.Delay ms={1250} /><br /></Typography>) })}
                    </Typist> :
                    <Loader />
                }
                <BackgroundSlider images={bgImages} duration={5} transition={2} />
            </Box>
            <Box width={'80%'} fontSize="0.6rem" m="auto" p={1} position="fixed" bottom="70px" left="10%" textAlign="center" borderRadius="10px" color="grey" bgcolor="rgba(255,255,255,0.2)">
                Notice: As a Westworld fan, I used some public images from the show that I found on the internet.
                In case of request due to copyright issues, they will be removed.
            </Box>
        </React.Fragment>
    );
};

export default HomePage;