import React, { useState, useEffect } from 'react';
import Button from '@material-ui/core/Button';
import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import { Box } from '@material-ui/core'
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import GithubIcon from '@material-ui/icons/GitHub';
import { Basics, BasicsData } from '../../appTypes';
import Loader from '../../components/Loader/Loader';
import LazyLoad from 'react-lazyload';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        large: {
            width: theme.spacing(20),
            height: theme.spacing(20),
        },
        buttons: {
            textTransform: "none"
        },        
    }),
);

const logos = [<GithubIcon fontSize="large"/>, <LinkedInIcon fontSize="large"/>];

const ContactMePage: React.FC<Basics> = (props): JSX.Element => {
    const classes = useStyles();

    const [basics, setBasics] = useState<BasicsData>();

    useEffect(() => {
        setBasics(props.data);
    }, [props.data]);

    if (basics !== undefined && basics !== null && basics.name !== '') {
        return (
            <React.Fragment>
                <Box display="flex" width={'80%'} m="auto" mt={2} justifyContent="center" alignContent="center" alignItems="center">
                    <LazyLoad once placeholder={<Loader />}>
                        <Avatar alt={basics?.name} src={basics?.picture} className={classes.large} />
                    </LazyLoad>
                </Box>
                <Box display="flex" flexWrap="wrap" justifyContent="center" p={1} m={1}>
                    {basics.profiles.map((profile, index) => (
                        <Box p={1} key={index}>
                            <Button
                                variant="outlined"
                                className={classes.buttons}
                                href={profile.url}
                                target="_blank"
                                startIcon={logos[index]}
                                size="large">{profile.network}<br />{profile.username}</Button>
                        </Box>
                    ))}
                </Box>
            </React.Fragment>
        )
    }
    else {
        return <Loader />
    }
};

export default ContactMePage;