import React, { useState, useEffect } from 'react';

import LazyLoad from 'react-lazyload';
import Loader from '../../components/Loader/Loader';

import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import OpenInNewIcon from '@material-ui/icons/OpenInNew';
import DetailsIcon from '@material-ui/icons/Details';

import Avatar from '@material-ui/core/Avatar';
import { Projects, Project } from '../../appTypes';
import { Chip } from '@material-ui/core';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        media: {
            backgroundSize: 'contain',
            height: 0,
            paddingTop: '40%', // 16:9
        },
        medium: {
            width: theme.spacing(10),
            height: theme.spacing(10),
        },
        buttons: {
            fontSize: '0.6rem',
            textTransform: "none"
        },
    }),
);

const ProjectsPage: React.FC<Projects> = (props): JSX.Element => {

    const classes = useStyles();

    const [list, setList] = useState<Array<Project>>([]);

    useEffect(() => {
        setList(props.data);
    }, [props.data]);

    return (
        <React.Fragment>
            <Box mt={2}>
                <Typography variant="subtitle1" color="textPrimary" component="h2">Featured Projects</Typography>
            </Box>
            <Box display="flex" flexWrap="wrap" justifyContent="flex-start" p={1} m={1}>
                {list.length > 0 ?
                    list.map((project, index) => (
                        <Box p={1} key={index} flexGrow={1}>
                            <Card>
                                <CardHeader
                                    avatar={<LazyLoad once placeholder={<Loader />}><Avatar
                                        variant="rounded"
                                        className={classes.medium}
                                        alt={project.displayName}
                                        src={project.logo} /></LazyLoad>}
                                    title={project.displayName}
                                    action={<Chip label={project.languages.join(', ')} size="small" />} />
                                <CardMedia
                                    className={classes.media}
                                    image={project.images[0].resolutions.thumbnail.url}
                                    title={project.displayName} />
                                <CardContent>
                                    <Typography variant="body2" color="textPrimary" component="p">
                                        {project.summary}
                                    </Typography>
                                </CardContent>
                                <CardActions>
                                    <Box width="100%" display="flex">
                                        <Box flexGrow={1}>
                                            <Button
                                                className={classes.buttons}
                                                href={'/#/projects/' + (index + 1)}
                                                startIcon={<DetailsIcon />}
                                                size="small"
                                                variant="outlined">Learn More</Button>
                                        </Box>
                                        <Box textAlign="right">
                                            <Button
                                                className={classes.buttons}
                                                href={project.website}
                                                startIcon={<OpenInNewIcon />}
                                                target="_blank"
                                                size="small"
                                                variant="outlined">Project Website</Button>
                                        </Box>
                                    </Box>
                                </CardActions>
                            </Card>
                        </Box>
                    )) : <Loader />}
            </Box>
            <Box mt={2} pb={2}>
                <Typography variant="subtitle1" color="textPrimary" component="h2">Others</Typography>
                <p>Under construction...</p>
            </Box>
        </React.Fragment>
    );
};

export default ProjectsPage;