import React, { useEffect, useState } from 'react';

import { HashRouter, Route, Switch, Redirect } from 'react-router-dom';

import axios from './axios';

import { Basics, Skills, Skill, Education, School, Projects, Experiences, Experience, Project, BasicsData } from './appTypes';

import { MuiThemeProvider } from '@material-ui/core/styles';
import theme from './components/Theme/Theme';

import Header from './components/Header/Header';
import PageTitle from './components/PageTitle/PageTitle';
import Footer from './components/Footer/Footer';

import CssBaseline from '@material-ui/core/CssBaseline';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';

import Error from './components/Error/Error';
import HomePage from './pages/HomePage/HomePage';
import AboutMePage from './pages/AboutMePage/AboutMePage';
import SkillsPage from './pages/SkillsPage/SkillsPage';
import EducationPage from './pages/EducationPage/EducationPage';
import ProjectsPage from './pages/ProjectsPage/ProjectsPage';
import ProjectPage from './pages/ProjectsPage/ProjectPage';
import ExperiencePage from './pages/ExperiencePage/ExperiencePage';
import ContactMePage from './pages/ContactMePage/ContactMePage';
import NotFoundPage from './pages/NotFoundPage/NotFoundPage';

const useStyles = makeStyles({
    container: {
        margin: '20px auto 50px',
    }
});

const App: React.FC = (): JSX.Element => {

    const [myBasics, setMyBasics] = useState<Basics>({ data: { headline: '', summary: '', picture: '', label: '', name: '', profiles: [] } });
    const [mySkills, setMySkills] = useState<Skills>({ data: [] });
    const [myEducation, setMyEducation] = useState<Education>({ schools: [], courses: [] });
    const [myProjects, setMyProjects] = useState<Projects>({ data: [] });
    const [myExperience, setMyExperience] = useState<Experiences>({ data: [] });
    const [error, setError] = useState<boolean>(false);
    const classes = useStyles();

    useEffect(() => {
        axios.get('/')
            .then(response => {

                // Set Basics
                let updatedBasics: BasicsData = response.data.basics;
                
                updatedBasics.profiles.shift();
                
                setMyBasics({ data: updatedBasics });

                // Set Skills
                let updatedSkilss: Array<Skill> = response.data.skills;

                updatedSkilss.map( (skill, index) => {
                    updatedSkilss[index].logo = '/images/skills/' + (index + 1) + '.png';
                    return null;
                });

                updatedSkilss[0].knownSince = 2014;
                updatedSkilss[1].knownSince = 2001;
                updatedSkilss[2].knownSince = 2019;
                updatedSkilss[3].knownSince = 2019;
                updatedSkilss[4].knownSince = 2004;
                updatedSkilss[5].knownSince = 2004;
                updatedSkilss[6].knownSince = 2019;
                updatedSkilss[7].knownSince = 2020;
                updatedSkilss[8].knownSince = 2006;

                setMySkills({ data:  updatedSkilss});

                // Set Education
                let updatedSchools: Array<School> = response.data.education;
                
                updatedSchools.map( (school, index) => {
                    updatedSchools[index].logo = '/images/education/' + (index + 1) + '.png';
                    return null;
                });

                updatedSchools[0].website = 'http://egitim.ankara.edu.tr';
                updatedSchools[1].website = 'http://www.science.ankara.edu.tr';
                updatedSchools[2].website = 'http://aal.meb.k12.tr';
                
                let courses: Array<string> = [
                    'Advanced Programming (C++)',
                    'Applied Mathematics',
                    'Cryptology',
                    'Differential Equations',
                    'Fortran',
                    'Java',
                    'Mathematical Modelling',
                    'Matlab',
                    'Modern Programming (PHP)',
                    'Motion Geometry',
                    'Numerical Analysis',
                    'Programming (C)',
                    'Quaternion Theory',
                    'Statistics',
                    'Transforms and Geometries'
                ];

                setMyEducation( { schools: updatedSchools, courses: courses});

                // Set Projects
                let updatedProjects: Array<Project> = response.data.projects;

                updatedProjects.map( (project, index) => {
                    updatedProjects[index].logo = '/images/projects/' + (index + 1) + '.png';
                    return null;
                });

                updatedProjects[0].description = 'notes goes here. notes goes here. notes goes here. notes goes here. notes goes here.';
                updatedProjects[1].description = 'notes goes here. notes goes here. notes goes here. notes goes here. notes goes here.';
                updatedProjects[2].description = 'notes goes here. notes goes here. notes goes here. notes goes here. notes goes here.';
                updatedProjects[3].description = 'notes goes here. notes goes here. notes goes here. notes goes here. notes goes here.';

                updatedProjects[0].gallery = {
                    caption: 'Here are the screenshots of Endeksa that I developed for the Neozeka Software Company in Ankara, Turkey in 2017.',
                    description: 
                        'This application that I contributed is extensive and significant.\n' + 
                        'It helped the Neozeka Software Company increase market share in real estate business.\n' +
                        'It\'s mainly used by real estate professionals to increase their accuracy rate for valuation and increase efficiency in their workflow.\n' +
                        'It\'s also used by individuals to get estimated valuation and useful analytics of their properties.\n' +
                        'I am thrilled about this website and its impact.'
                };
                updatedProjects[1].gallery = {
                    caption: 'Here are screenshots of AppMerket Developer Portal that I developed for the BaÅarÄ± Mobile Company in Ankara, Turkey in 2014.',
                    description: 'Description goes here'
                };
                updatedProjects[2].gallery = {
                    caption: 'Here are screenshots of Oyunus that I developed for an individual company in Ankara, Turkey in 2013.',
                    description: 'Description goes here'
                };
                updatedProjects[3].gallery = {
                    caption: 'Here are screenshots of Mitos that I developed for the Mikrobeta Software Company in Ankara, Turkey in 2011.',
                    description: 'Description goes here'
                };               

                setMyProjects({ data:  updatedProjects});

                // Set Experience
                let updatedExperiences: Array<Experience> = response.data.work;
                
                updatedExperiences.map( (experience, index) => {
                    updatedExperiences[index].logo = '/images/experience/' + (index + 1) + '.png';
                    return null;
                });

                setMyExperience({ data:  updatedExperiences});

            })
            .catch(error => {
                setError(true);
            });
    }, []);

    let content = (
        <Switch>
            <Route exact path='/' render={(props) => <HomePage data={myBasics.data} />} />
            <Route exact path='/about-me' render={(props) => <AboutMePage data={myBasics.data} />} />
            <Route exact path='/skills' render={(props) => <SkillsPage data={mySkills.data} />} />
            <Route exact path='/education' render={(props) => <EducationPage schools={myEducation.schools} courses={myEducation.courses} />} />
            <Route exact path='/projects' render={(props) => <ProjectsPage data={myProjects.data} />} />
            <Route path='/projects/:id' render={(props) => <ProjectPage data={myProjects.data[props.match.params.id - 1]} />} />
            <Route exact path='/experience' render={(props) => <ExperiencePage data={myExperience.data} />} />
            <Route path='/contact-me' render={(props) => <ContactMePage data={myBasics.data} />} />
            <Route path='/404' component={NotFoundPage} />
            <Redirect to='/404' />
        </Switch>
    );

    if (error) {
        content = <Error />;
    }

    return (
        <MuiThemeProvider theme={theme}>
            <HashRouter>
                <CssBaseline>
                    <Header />
                    <Container maxWidth="xl" className={classes.container}>
                        <PageTitle />
                        {content}
                    </Container>
                    <Footer />
                </CssBaseline>
            </HashRouter>
        </MuiThemeProvider>
    );
};

export default App;