import React, { useEffect, useState } from 'react';
import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import { Box } from '@material-ui/core'
import { Basics } from '../../appTypes';
import Loader from '../../components/Loader/Loader';
import LazyLoad from 'react-lazyload';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        large: {
            width: theme.spacing(20),
            height: theme.spacing(20),
        },
    }),
);


const AboutMePage: React.FC<Basics> = (props): JSX.Element => {

    const classes = useStyles();

    const [list, setList] = useState<Array<string>>([]);

    useEffect(() => {
        if (props.data.summary !== '') {
            setList(props.data.summary.split('\n'));
        }
    }, [props.data]);

    return (
        <Typography component="div">
            <Box width={'80%'} m="auto" mt={2}>
                <Typography variant="subtitle1" color="textPrimary" component="h2">Summary</Typography>
                {list.length > 0 ?
                    list.map((text, index) => (
                        <Box key={index} m={1}>
                            - {text}
                        </Box>
                    )) :
                    <Loader />
                }
                <Box display="flex" m="auto" p={2} justifyContent="center" alignContent="center" alignItems="center">
                    <LazyLoad placeholder={<Loader />}><Avatar alt={props.data.name} src={props.data.picture} className={classes.large} /></LazyLoad>
                </Box>
            </Box>
        </Typography>
    );
};

export default AboutMePage;