import React, { useState, useEffect } from 'react';

import { Skills, Skill } from '../../appTypes';

import LazyLoad from 'react-lazyload';
import Loader from '../../components/Loader/Loader';

import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';

import Avatar from '@material-ui/core/Avatar';

import Rating from '@material-ui/lab/Rating';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        medium: {
            width: theme.spacing(10),
            height: theme.spacing(10),
        },
    }),
);

const SkillsPage: React.FC<Skills> = (props): JSX.Element => {
    const classes = useStyles();

    const [list, setList] = useState<Array<Skill>>([]);

    useEffect(() => {
        setList(props.data);
    }, [props.data]);

    return (
        <React.Fragment>
            <Box mt={2}>
                <Typography variant="subtitle1" color="textPrimary" component="h2">Featured Skills</Typography>
            </Box>
            {list.length > 0 ?
                <Box display="flex" flexWrap="wrap" justifyContent="flex-start" p={1} m={1}>
                    {list.map((skill, index) => (
                        <Box p={1} key={index} flexGrow={1}>
                            <Card>
                                <CardHeader avatar={<LazyLoad placeholder={<Loader />}><Avatar
                                    variant="rounded"
                                    className={classes.medium}
                                    alt={skill.name}
                                    src={skill.logo} /></LazyLoad>}
                                    title={skill.name} subheader={"known since " + skill.knownSince} />
                                <CardContent>
                                    <Typography variant="body2" color="textSecondary" component="p">
                                        {skill.yearsOfExperience} year(s) professional work experience
                                    </Typography>
                                </CardContent>
                                <CardActions>
                                    <Rating name="read-only" value={skill.rating} precision={0.5} readOnly />
                                </CardActions>
                            </Card>
                        </Box>
                    ))}
                </Box> :
                <Loader />
            }
            <Box mt={2} pb={2}>
                <Typography variant="subtitle1" color="textPrimary" component="h2">Certificates</Typography>
                <ul>
                    <li>ROS For Beginners Path from The Construct</li>
                    <li>MCSD: App Builder from Microsoft</li>
                    <li>Programming in HTML5 with Javascript and CSS3 Specialist</li>
                    <li>MCP from Microsoft</li>
                    <li>MTA from Microsoft</li>
                </ul>
            </Box>
            <Box mt={2} pb={2}>
                <Typography variant="subtitle1" color="textPrimary" component="h2">Languages</Typography>
                <ul>
                    <li>Turkish: Native or bilingual proficiency</li>
                    <li>English: Professional working proficiency</li>
                </ul>
            </Box>
            <Box mt={2} pb={2}>
                <Typography variant="subtitle1" color="textPrimary" component="h2">Others</Typography>
                <p>Under construction...</p>
            </Box>
        </React.Fragment>
    );
};

export default SkillsPage;