import React, { useEffect, useState } from 'react';

import LazyLoad from 'react-lazyload';
import Loader from '../../components/Loader/Loader';

import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import clsx from 'clsx';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Avatar from '@material-ui/core/Avatar';
import PhotoLibraryIcon from '@material-ui/icons/PhotoLibrary';
import BackIcon from '@material-ui/icons/ArrowBack';
import OpenInNewIcon from '@material-ui/icons/OpenInNew';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

import { Project, ProjectDetail } from '../../appTypes';

import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css';
import { CardActionArea, Chip, Divider, IconButton, Collapse, ListItem, List, ListItemText } from '@material-ui/core';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        card: {
            width: '80%',
            margin: '0 auto'
        },
        media: {
            backgroundSize: 'contain',
            height: 0,
            paddingTop: '30%', // 16:9
        },
        buttons: {
            fontSize: '0.6rem',
            textTransform: "none"
        },
        medium: {
            width: theme.spacing(5),
            height: theme.spacing(5),
        },
        expand: {
            border: '1px solid #bbb',
            transform: 'rotate(0deg)',
            marginLeft: 'auto',
            transition: theme.transitions.create('transform', {
                duration: theme.transitions.duration.shortest,
            }),
        },
        expandOpen: {
            transform: 'rotate(180deg)',
        },
    }),
);

const ProjectPage: React.FC<ProjectDetail> = (props): JSX.Element => {

    const classes = useStyles();

    const [projectData, setProjectData] = useState<Project | undefined>();

    const [photoIndex, setPhotoIndex] = useState<number>(0);
    const [isOpen, setIsOpen] = useState<boolean>(false);

    const [expanded, setExpanded] = React.useState(false);

    useEffect(() => {
        setProjectData(props.data);
    }, [props.data]);


    const handleExpandClick = () => {
        setExpanded(!expanded);
    };

    if (projectData !== undefined && projectData !== null) {
        return (
            <React.Fragment>
                <Box display="flex" mt={2}>
                    <Box flexGrow={1}>
                        <Typography variant="subtitle1" color="textPrimary" component="h2">
                            Project: {projectData?.displayName}
                        </Typography>
                    </Box>
                    <Box textAlign="right">
                        <Button
                            href={'/#/projects'}
                            className={classes.buttons}
                            size="small"
                            startIcon={<BackIcon />}
                            variant="outlined">Back to All Projects</Button>
                    </Box>
                </Box>
                <Divider />
                <Box m={1} mt={2} pb={2}>
                    <Card className={classes.card}>
                        <CardHeader
                            title={projectData?.displayName}
                            action={<Chip label={projectData?.languages.join(', ')} size="small" />}
                            avatar={<LazyLoad once placeholder={<Loader />}><Avatar
                                variant="rounded"
                                className={classes.medium}
                                alt={projectData?.displayName}
                                src={projectData?.logo} /></LazyLoad>}>
                        </CardHeader>
                        <CardActionArea onClick={() => setIsOpen(true)}>
                            <CardMedia
                                className={classes.media}
                                image={projectData?.images[0].resolutions.mobile.url}
                                title={projectData.displayName} />
                            <CardContent>
                                <Typography align="center" variant="h6" component="h3">
                                    {projectData?.gallery?.caption}
                                </Typography>
                                {projectData?.gallery?.description
                                    .split('\n')
                                    .map( (sentence, index) => 
                                    <Typography key={index} align="center" variant="body2" component="p">{sentence}</Typography>
                                    )   
                                }
                            </CardContent>
                        </CardActionArea>
                        <CardActions>
                            <Box width="100%" display="flex">
                                <Box flexGrow={1}>
                                    <Button
                                        variant="outlined"
                                        className={classes.buttons}
                                        onClick={() => setIsOpen(true)}
                                        startIcon={<PhotoLibraryIcon />}
                                        size="small"
                                    >Open Gallery</Button>
                                    <Button
                                        className={classes.buttons}
                                        href={projectData?.website}
                                        startIcon={<OpenInNewIcon />}
                                        target="_blank"
                                        size="small"
                                        variant="outlined">Website</Button>
                                </Box>
                                <Box textAlign="right">
                                    <IconButton
                                        size="small"
                                        className={clsx(classes.expand, {
                                            [classes.expandOpen]: expanded,
                                        })}
                                        onClick={handleExpandClick}
                                        aria-expanded={expanded}
                                        aria-label="Technical Details">
                                        <ExpandMoreIcon />
                                    </IconButton>
                                </Box>
                            </Box>
                        </CardActions>
                        <Collapse in={expanded} timeout="auto" unmountOnExit>
                            <CardContent>
                                <Divider />
                                <Typography variant="subtitle1" color="textSecondary" component="h4">
                                    Technical Details
                                </Typography>
                                <List>
                                    <ListItem>
                                        <ListItemText
                                            primary="My Role"
                                            secondary="Backend Developer" />
                                    </ListItem>
                                    <ListItem>
                                        <ListItemText
                                            primary="Architecture"
                                            secondary="Monolitich" />
                                    </ListItem>                  
                                    <ListItem>
                                        <ListItemText
                                            primary="Languages"
                                            secondary={projectData?.languages.join(', ')} />
                                    </ListItem>   
                                    <ListItem>
                                        <ListItemText
                                            primary="Frameworks / Libraries"
                                            secondary={projectData?.libraries.join(', ')} />
                                    </ListItem>   
                                    <ListItem>
                                        <ListItemText
                                            primary="Notes"
                                            secondary={projectData?.description} />
                                    </ListItem>                                                                                                                            
                                </List>
                            </CardContent>
                        </Collapse>
                    </Card>
                    {isOpen ?
                        <Lightbox
                            mainSrc={projectData?.images[photoIndex].resolutions.desktop.url}
                            nextSrc={projectData?.images[(photoIndex + 1) % projectData?.images.length].resolutions.desktop.url}
                            prevSrc={projectData?.images[(photoIndex + projectData?.images.length - 1) % projectData?.images.length].resolutions.desktop.url}
                            onCloseRequest={() => setIsOpen(false)}
                            onMovePrevRequest={() =>
                                setPhotoIndex((photoIndex + projectData?.images.length - 1) % projectData?.images.length)
                            }
                            onMoveNextRequest={() =>
                                setPhotoIndex((photoIndex + 1) % projectData?.images.length)
                            }
                        /> : null
                    }
                </Box>
            </React.Fragment>
        )
    }
    else {
        return <Loader />;
    }
};

export default ProjectPage;